<template>
  <div class="home">
    <img alt="Vue logo" src="../assets/logo.png">
    <HelloWorld msg="Vue.js 안녕하세요." />
    {{ input1 }} <!-- {{ 문자열 }} 이런 문법을 머스타치 문법 이라고 부른다 뷰에 데이터 연결 -->
    <h2 v-html="input1"></h2>
    <input type="text" v-model="input1" />&nbsp;
    <p v-if="input1.length > 10">메시지: 길이가 10글자 이상입니다. {{ input1 }}</p>
    <button type="button" @click="getData">Get11</button>
    <img v-bind:src="imageSource" alt="random">
    <a :href="naverUrl">네이버</a>
    &nbsp;

    <h2 :class="{ red: 1 <= completedTodos && completedTodos <= 3 , blue: 3 <= completedTodos && completedTodos <= 7}"> 
      배열의 갯수는 {{ completedTodos }} 입니다.
    </h2>
    <ul>
      <li :key="index" v-for="(data, index) in frameworks">
        {{ data.title }}
        /
        {{ data.completed }}
        /
        <button @click="complatedTodo(index)">완료</button>
      </li>
    </ul>
    <button type="button" @click="addItem">추가</button>
    <button type="button" @click="delItem">삭제</button>
    <button type="button" @click="fetchTodos(i)">fetch 사용하기</button>
    <br><br>
    <table border="1">
      <tr>
        <td>11</td>
        <td>44</td>
      </tr>
    </table>
  </div> 
</template>
<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue';
import axios from 'axios';

export default {
  //메소드형태
  data() {
      return {
        fruits: ["banana", "apple", "ornage"],
        display: true,
        age: 11,
        naverUrl: "http://www.naver.com",
        imageSource: "https://placeimg.com/100/100/any",
        input1: "<b>헬로우 world 11</b>",
        options: [
          {v:"s",t:"서울"},
          {v:"j",t:"jeju"},
          {v:"b",t:"busan"},
        ],
        reginon: "j",
        tableShow: true,
        frameworks: [
          {title: 'aaa', completed: false},
          {title: 'bbb', completed: false},
        ],
      };
  },
  //간단한 계산을 도와주는 함수
  //배열이 변경되면 계산을 해서 리턴해주는 함수
  computed: {
    completedTodos() {
      return this.frameworks.filter(framework=> framework.completed === true).length
    }
  },
  //변수값이 변경되면 자동으로 실행되는 함수
  watch: {
    input1() {
      console.log(this.input1);      
    }
  },
  methods: {
    getData() {
      axios.get("/api/helloInsert", { 
        params: {}
      }).then((response) => {        
        console.log("response.data", response.data);
      }).catch((error) => {
        console.log(error.response.data);
        console.log(error.response.status);
        console.log(error.response.headers);
      });
    },
    addItem() {
      this.frameworks.push({
        title: this.input1,
        completed: true,
      });
    },
    delItem() {
      this.frameworks.pop();
    },
    changeRegion() {
      alert(this.reginon);
    },
    fetchTodos() {
      //get방식 ajax 호출
      fetch("https://jsonplaceholder.typicode.com/todos?_start=0&_end=30")
      .then(res=> res.json())
      .then(data=> {
        this.frameworks = data;
        console.log(data);
      })
      .catch((error)=> {
        console.log(error);
      })
    },
    complatedTodo(index) {
      this.frameworks[index].completed = true;
    }
  },
  components: {
    HelloWorld
  },
  beforeCreate() {
    console.log("1번째");
  },
  created() {
    console.log("2번째");
  },
  beforeMount() {
    console.log("3번째");
  },
  mounted() {
    console.log("4번째");
  }
}
</script>
